.header {
  width: 100%;
  color: #fbfbfb;
  position: absolute;
  z-index: 1;
  top: 0;
}
.header .home-page {
  padding: 0 24px;
}
.header-logo {
  display: inline-block;
  position: relative;
  width: 150px;
  line-height: 64px;
}
.header-menu {
  float: right;
}
.header-menu .ant-menu-horizontal {
  border-bottom: none;
}
.header-menu .ant-menu {
  color: #d1d0d0;
  line-height: 62px;
  height: 64px;
  background: transparent;
}
.header-menu .ant-menu-item-selected,
.header-menu .ant-menu-item:hover,
.header-menu .ant-menu-item-active,
.header-menu .ant-menu-submenu-active {
  color: white;
  border-bottom: 2px solid white !important;
}
.header-menu .ant-menu-submenu-title:hover {
  color: white;
}
@media screen and (max-width: 767px) {
  .header-logo {
    z-index: 101;
  }
  .header .home-page {
    padding: 0 24px;
  }
  .header-menu {
    height: auto;
    float: inherit;
    position: relative;
    left: -24px;
    width: calc(100% + 48px);
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    background: #fff;
  }
  .header-mobile-menu {
    width: 16px;
    height: 14px;
    cursor: pointer;
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 100;
  }
  .header-mobile-menu em {
    display: block;
    width: 100%;
    height: 2px;
    background: #001529;
    margin-top: 4px;
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  .header-mobile-menu :first-child {
    margin-top: 0;
  }
  .header .ant-menu {
    height: auto;
    overflow: hidden;
  }
  .header .ant-menu .ant-menu-item-selected {
    border: none;
  }
  .header .open {
    height: auto;
  }
  .header .open .header-mobile-menu em:nth-child(1) {
    transform: translateY(6px) rotate(45deg);
  }
  .header .open .header-mobile-menu em:nth-child(2) {
    opacity: 0;
  }
  .header .open .header-mobile-menu em:nth-child(3) {
    transform: translateY(-6px) rotate(-45deg);
  }
  .header .open > .header-menu {
    opacity: 1;
    pointer-events: auto;
  }
}
